const initialState = {
    paymentStatus: null,
    //   
  };
  
  const paymentReducer = (state = initialState, action) => {
    switch (action.type) {
      case 'PAYMENT_SUCCESS':
        return {
          ...state,
          paymentStatus: 'success',
          //    
        };
      case 'PAYMENT_CANCEL':
        return {
          ...state,
          paymentStatus: 'cancelled',
          //    
        };
      //   
      default:
        return state;
    }
  };
  
  export default paymentReducer;
  