import React from 'react';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
/* css import */
import style from '../../styles/Admin/AdminHeader.module.css';


const Header = () => {
  const dispatch = useDispatch();

  return (
	<header>	
		<div className={style.header}>
			<h1 className={style.logo}>
				<Link to="/Admin/AdminHome"><span></span></Link>
			</h1>
			<div className={style.nav}>
				<Link to="/Admin/Category"> </Link> 
				<Link to="/Admin/Items"> </Link>
				<Link to="/Admin/Tags"> </Link>
				<Link to="/Admin/Events"> </Link>
			</div>
			<Link to="/Home" className={style.pointAdmin} target='_blank'><span className='material-icons'>home</span><p></p></Link>
		</div>
	</header>
  )
}

export default Header;