import React, {useState} from 'react';
import axios from 'axios';
import { useDispatch } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
// css import
import style from '../styles/Auth.module.css'

const Contactus = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [submitName, setSubmitName] = useState('');
  const [submitEmail, setSubmitEmail] = useState('');
  const [title, setTitle] = useState('');
  const [contents, setContents] = useState('');

  const [IsNameVal, setIsNameVal] = useState(false);
  const [isEmailVal, setValidEmail] = useState(false);
  const [errorMessageName, setErrorMessageName] = useState('');
  const [errorMessage, setErrorMessage] = useState('');

  //   
  const handleChangeName = (newName) => {
    const regName = /^[-]{1,}$/;
    const isNameReg = regName.test(newName);

    if (!isNameReg){
      setIsNameVal(false);
    } else {
      setIsNameVal(true);
    }
  
    setSubmitName(newName);
  }
  //   
  const handleChangeEmail = (newEmail) => {
    const regEmail = /^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Z|a-z]{2,}$/;
    const isEmailReg = regEmail.test(newEmail);
    if (!isEmailReg){
      setErrorMessage('   .');
    } else {
      setErrorMessage('');
    }
    setValidEmail(isEmailReg);
  }

  //   
  const contact = async(submitData) => {
    try {
      const response = await axios.post('/contact_us', submitData);
      if (response.status === 200) {
        alert(`   ! !`);
        window.location.reload();
      } else {
        alert(`    .     .`);
      }
    } catch (error) {
      console.log('Error mailing :', error);
      alert(`    .     .`);
    }
  }; 
  const handleContact = (e) => {
    e.preventDefault();
    const submitData = {
      submitName: submitName,
      submitEmail: submitEmail,
      title: title,
      contents: contents,
    };
    if (submitName === '') {
      alert(' .')
    } else if (submitEmail === '') {
      alert('  .')
    } else if (!isEmailVal) {
      alert('   .')
    } else if (title === '') {
      alert(' .')
    } else if (contents === '') {
      alert(' .')
    } else {
      contact(submitData);
    }
  };

  return (
    <section className={`${style.contactus} ${style.auth}`}>
      <div className='webwidth webwidth_pd'>
        <div className='page_tit'><h2>  </h2></div>
        <div className={style.form}>
          <form onSubmit={ handleContact }>
            <div className={style.inputwrap}>
              <div className={style.inputHalf}>
                <input type='text' name='submitName' placeholder=' .'
                  value={submitName}
                  onChange={(e) => {
                    setSubmitName(e.target.value)
                    handleChangeName(e.target.value);
                  }} />
                {errorMessageName && <p style={{ marginTop: '8px', color: 'red' }}>{errorMessageName}</p>}
              </div>
              <div className={style.inputHalf}>
                <input type='text' name='submitEmail' placeholder='  .'
                  value={submitEmail}
                  onChange={(e) => {
                    setSubmitEmail(e.target.value)
                    handleChangeEmail(e.target.value);
                  }} />
                {errorMessage && <p style={{ marginTop: '8px', color: 'red' }}>{errorMessage}</p>}
              </div>
              <input type='text' name='title' placeholder=' .'
                  value={title}
                  onChange={(e) => { setTitle(e.target.value) }} />
              <textarea type='text' name='contents' placeholder=' .'
                  value={contents}
                  onChange={(e) => { setContents(e.target.value) }} />
            </div>
            <div className={style.doublebutton}>
              <Link className='btn btn_normal' onClick={ ()=>{ navigate(-1) } }> </Link>
              <button type='submit' className='btn btn_full'> </button>
            </div>
          </form>
        </div>
      </div>
    </section>
  )
}

export default Contactus;