export const join = (userData, navigate) => async(dispatch) => {
  const {
    memberName,
    loginId,
    password,
    passwordCheck,
    gender,
    age,
    phoneNumber,
    emailValNumber,
    isPassval,
    isPhoneval,
    fetchedEmailValNumber,
  } = userData;
  
  if(loginId === ''){ alert('   .')
  } else if(emailValNumber === fetchedEmailValNumber) {
    alert(' .')
    dispatch(setEmailValNumber(''));
  } else if(!isPassval) {
    alert('  .')
    dispatch(setPassword(''));
    dispatch(setPasswordCheck(''));
  } else if(memberName === ''){ alert(' .')
  } else if(age === ''){ alert(' .')
  } else if (gender === ' .' || gender === ''){ alert(' .')
  } else if (!isPhoneval){
    alert('   .')
    dispatch(setPhoneNumber(''));
  } else {
    try{
      const response = await axios.post('/member/register', {
        memberName: memberName,
        loginId: loginId,
        password: password,
        passwordCheck: passwordCheck,
        gender: gender,
        
        age: age,
        phoneNumber: phoneNumber,
      });
      if (response.status === 200) {
        alert(` .  .`)
        navigate('/Auth/Login');
      } else {
        alert(` .   .`)
        // window.location.reload();
      }
    } catch (error) {
      console.log('Error Member Register :', error);
      alert(`   .     .`)
    }
  }
}
export const joinAfter = (userData, navigate) => async(dispatch) => {
  const {
    access,
    memberName,
    gender,
    age,
  } = userData;
  
  if(!access){
    alert('   .   .');
    navigate('/Auth/Join');
  } else {
    try{
      const response = await axios.post('/member/oauth/update', {
        access: access,
        memberName: memberName,
        gender: gender,
        age: age,
      });
      if (response.status === 200) {
        alert(` .  .`)
        dispatch(logout());
        navigate('/Auth/Login');
      } else {
        alert(` .   .`)
      }
    } catch (error) {
      console.log('Error Member Register :', error);
      alert(`   .     .`)
    }
  }
}
