import React from 'react'
import { useEffect } from "react";
import { useLocation } from "react-router-dom";

export default function Scrolltop() {
  const { pathname } = useLocation();

  useEffect(() => {
    //        
    window.scrollTo(0, 0);
    //  unmount    
    return () => {
      window.scrollTo(0, 0);
    };
  }, [pathname]);

  return null;
}