const initialState = {
  categories: [
    // { categoryEng: 'ott', category: '', icon: '/images/icons/ott.svg' },
    // { categoryEng: 'music', category: '', icon: '/images/icons/music.svg' },
    // { categoryEng: 'food', category: '', icon: '/images/icons/food.svg' },
    // { categoryEng: 'health', category: '', icon: '/images/icons/health.svg' },
    // { categoryEng: 'drink', category: '', icon: '/images/icons/drink.svg' },
    // { categoryEng: 'alcohol', category: '', icon: '/images/icons/alcohol.svg' },
    // { categoryEng: 'extra', category: '', icon: '/images/icons/extra.svg' },
    // { categoryEng: 'book', category: '', icon: '/images/icons/book.svg' },
    // { categoryEng: 'ride', category: '', icon: '/images/icons/ride.svg' },
    // { categoryEng: 'plant', category: '', icon: '/images/icons/plant.svg' },
    // { categoryEng: 'pet', category: '', icon: '/images/icons/love.svg' },
  ],
  featuredcategories: [
    { categoryEng: 'best', category: '',},
  ],
  othercategories:[
    { categoryEng: 'compare', category: '',},
    { categoryEng: 'event', category: '',},
    { categoryEng: 'contactus', category: 'Contact Us',},
  ],
  dropCategory: false,
};


const categoryReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'FETCH_CATEGORY':
      return {
        ...state,
        categories: action.payload,
      };
    case 'SHOW_CATEGORY':
      return {
        ...state,
        dropCategory: !state.dropCategory,
      };

    default:
      return state;
  }
};

export { categoryReducer };